//
// user-variables.scss
// Use this to override Bootstrap and Silicon variables
//

// Example of a variable override to change Silicon background color
// Remove the "//" to comment it in and see it in action!
@import "bootstrap/scss/functions";
@import "bootstrap/scss/mixins";

$grid-breakpoints: (
  xs: 0,
  350: 350px,
  370: 370px,
  400: 400px,
  420: 420px,
  435: 435px,
  sm: 450px,
  480: 480px,
  500: 500px,
  530: 530px,
  md: 740px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
  xxxl: 2000px,
  xxl3: 3000px
) !default;

$container-max-widths: (
  xs: 95%,
  lg: 70%
);

$dark: #111111;
$body-bg: $dark;
$body-dark-mode-bg: $dark;
$offcanvas-dark-mode-bg-color: $dark;

$light: #fff;
$light-primary: #1a7dff;
$primary: #0271ff;
$dark-primary: #0262e4;
$success: #00f78b;
$secondary: #747474;
$gray: #999;
$dark: #181818;

$spacer:                      1rem;

$spacers: (
  0: 0,
  1: $spacer * .25,
  2: $spacer * .65,
  3: $spacer,
  4: $spacer * 1.5,
  5: $spacer * 2.5,
  6: $spacer * 4,
  7: $spacer * 7,
  8: $spacer * 8.5,
  9: $spacer * 9.5,
  10: $spacer * 10.5
);

$border-light-color:                        rgba($gray, 1) !default;
